.btn:hover,
.btn:active,
.btn:focus {
  background-color: var(--neutral-pale-orange);
  color: white;
}
.btn {
  display: block;
  padding: 10px 25px;
  width: fit-content;
  font-weight: 700;
  transition: 0.3s ease-out;
  transition-property: color, background;
  cursor: pointer;
}

.btn.inverted:hover,
.btn.inverted:active,
.btn.inverted:focus {
  background-color: white;
  color: var(--neutral-pale-orange);
}
.btn:focus,
.btn:active {
  outline: 1px dotted black;
}

.shadow {
  box-shadow: 0px 10px 20px -10px var(--primary-orange);
}
