@import url(https://fonts.googleapis.com/css2?family=Be+Vietnam:wght@400;500;700&display=swap);
:root {
  /** media queries */
  --desktop-width: 1000px;

  /**   colors    */
  --primary-orange: hsl(12, 88%, 59%);
  --primary-blue: hsl(228, 39%, 23%);
  /* neutral */
  --neutral-pale-orange: hsl(12, 80%, 65%);
  --neutral-pale-blue: hsl(227, 12%, 61%);
  --neutral-dark-blue: hsl(233, 12%, 13%);
  --neutral-pale-red: hsl(13, 100%, 96%);
  --neutral-pale-gray: hsl(0, 0%, 98%);

  /* page padding */
  --padding-horizontal-mobile:20px;  
  --padding-horizontal-desktop:120px;  
}

/*** font ***/
body{
  font-family: 'Be Vietnam', sans-serif;
  font-size: 16px;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role="list"],
ol[role="list"] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
  scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    -webkit-animation-duration: 0.01ms !important;
            animation-duration: 0.01ms !important;
    -webkit-animation-iteration-count: 1 !important;
            animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: hsl(228, 45%, 44%);
}
.App {
  text-align: center;
  color: var(--primary-blue);
  background-image: 
    url(../../static/media/bg-tablet-pattern.b6990a98.svg),
    url(../../static/media/bg-tablet-pattern.b6990a98.svg);
  background-repeat: no-repeat;
  background-position: calc(50% + 120px) -20px, calc(100% + 150px)  560px;
  /* background-position: 80px -60px, 180px 680px; */

  background-size: 430px, 290px;
  
}

.App > * {
  padding-left: var(--padding-horizontal-mobile);
  padding-right: var(--padding-horizontal-mobile);
}

/**** Global styles *****/

a[class] {
  text-decoration: none;
}

.round {
  padding: 10px 30px;
  border-radius: 100px;
  background-color: var(--primary-orange);
  color: white;
  border: none;
}

.round.inverted {
  background-color: white;
  color: var(--primary-orange);
}

.dialog {
  border-radius: 5px;
}

h1 {
  font-size: 2em;
}
h2 {
  font-size: 1.6em;
}

h1,
h2 {
  margin-bottom: 10px;
  margin-top: 10px;
}
h3 {
  margin-bottom: 20px;
  margin-top: 20px;
}
@media screen and (min-width: 1000px) {
  .App {
    background-position: calc(75% + 340px) -150px, -650px 1000px;
    background-size: 800px, 900px;
  
  }
  .App > * {
    padding-left: var(--padding-horizontal-desktop);
    padding-right: var(--padding-horizontal-desktop);
  }
  h1 {
    font-size: 3.8em;
  }
  h2 {
    font-size: 2.8em;
  }
  h1,
  h2 {
    margin-top: 60px;
    margin-bottom: 50px;
    line-height: 1;
  }
}

.app-header {
  position: -webkit-sticky;
  position: sticky;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  top: 0;
  background: rgb(255, 255, 255, 0.75);
  background: linear-gradient(
    0,
    rgba(255, 255, 255, 0.7) 0%,
    rgba(255, 255, 255, 1) 100%
  );
  padding-top: 60px;
  padding-bottom: 25px;
  margin-bottom: 80px;
  z-index: 1;
}
header > .logo {
  float: left;
}

.hamburger {
  background: none;
  border: none;
}

.app-header > .btn {
  display: none;
}
nav {
  clear: both;
  background: white;
  width: 90%;
  position: absolute;
  top: 115px;
  left: 50%;
  opacity: 0;
  -webkit-transform: translate(-50%, -500px);
          transform: translate(-50%, -500px);
  transition: opacity 1s ease-out, -webkit-transform 0.6s ease;
  transition: transform 0.6s ease, opacity 1s ease-out;
  transition: transform 0.6s ease, opacity 1s ease-out, -webkit-transform 0.6s ease;
}
.nav-item:first-child {
  margin-top: 30px;
}
.nav-item:last-child {
  margin-bottom: 30px;
}
.nav-item {
  display: block;
  color: inherit;
  font-weight: 500;
  transition: color 0.2s;
}
.nav-item:hover {
  color: var(--neutral-pale-blue);
}
.nav-item + .nav-item {
  margin-top: 20px;
}
.nav-blackout {
  z-index: -1;
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  background: rgb(255, 255, 255);
  background: linear-gradient(180deg, transparent 0%, rgba(0, 0, 0, 0.5) 100%);
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  transition: -webkit-transform 1s cubic-bezier(1, 0, 1, 1);
  transition: transform 1s cubic-bezier(1, 0, 1, 1);
  transition: transform 1s cubic-bezier(1, 0, 1, 1), -webkit-transform 1s cubic-bezier(1, 0, 1, 1);
}
.nav-blackout.show {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
nav.show {
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  opacity: 1;
}
@media screen and (min-width: 1000px) {
  nav {
    background: none;
    position: absolute;
    top: 50%;
    width: auto;
    opacity: 1;
    opacity: initial;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
  }
  .nav-item {
    margin: 0 !important;
    padding: 10px;
  }
  .hamburger {
    display: none;
  }
  .app-header > .btn {
    display: block;
  }

  .nav-item {
    display: inline-block;
    margin: 0 15px;
  }
}

.btn:hover,
.btn:active,
.btn:focus {
  background-color: var(--neutral-pale-orange);
  color: white;
}
.btn {
  display: block;
  padding: 10px 25px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-weight: 700;
  transition: 0.3s ease-out;
  transition-property: color, background;
  cursor: pointer;
}

.btn.inverted:hover,
.btn.inverted:active,
.btn.inverted:focus {
  background-color: white;
  color: var(--neutral-pale-orange);
}
.btn:focus,
.btn:active {
  outline: 1px dotted black;
}

.shadow {
  box-shadow: 0px 10px 20px -10px var(--primary-orange);
}

footer {
  background-color: var(--neutral-dark-blue);
  padding-top: 60px;
  color: var(--neutral-pale-gray);
}
footer a:focus,
footer .btn:focus {
  outline-color: white;
}
.link {
  color: inherit;
  transition: 0.2s color;
}
.link:hover {
  color: var(--primary-orange);
}
.logo > g > path {
  fill: var(--neutral-pale-gray);
}
.footer-grid > *:not(:first-child) {
  margin-top: 50px;
  margin-bottom: 50px;
}
.footer-grid > .article-text {
  font-size: 0.8em;
}
@media screen and (min-width: 1000px) {
  .footer-grid > *:not(:first-child) {
    margin: 0;
  }
  .footer-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: auto auto;
    grid-template-areas:
      "logo nav nav input"
      "social nav nav copyright";
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
    grid-row-gap: 60px;
    row-gap: 60px;
  }
  .footer-grid > .text-form {
    grid-area: input;
  }
  .footer-grid > .social-medias {
    grid-area: social;
  }
  .footer-grid > .logo {
    grid-area: logo;
    margin: auto;
  }
  .footer-grid > .multi-columns {
    grid-area: nav;
  }
  .footer-grid > .article-text {
    grid-area: copyright;
    text-align: right;
  }
}

.text-form {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.text-form > .input {
  background: white;
  color: var(--primary-blue);
  margin-right: 10px;
  width: 25ch;
}

.text-form > * {
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  -webkit-flex-grow: 0;
          flex-grow: 0;
}

.article-section {
  margin-top: 60px;
}
.article-text {
  color: var(--neutral-pale-blue);
  margin-bottom: 45px;
}
.article-text.compact {
  margin-right: 15px;
  margin-left: 15px;
}
.article-header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  background-color: var(--neutral-pale-red);
  border-radius: 100px 0 0 100px;
  margin-right: calc(-1 * var(--padding-horizontal-mobile));
  margin-bottom: 10px;
}
.article-header > h4 {
  display: block;
  width: 100%;
  margin-left: 15px;
}
.article-section:first-of-type {
  counter-reset: article;
}
.article-header::before {
  counter-increment: article;
  content: counters(article, ".", decimal-leading-zero);
  background-color: var(--primary-orange);
  padding: 5px;
  text-align: center;
  width: 60px;
  border-radius: 50px;
  color: white;
  font-weight: bold;
}
.article-section {
  text-align: left;
}

@media screen and (min-width: 1000px) {
  .article-header {
    background: none;
  }
  .article-header > h4 {
    margin-left: 30px;
  }
  .article-section > .article-text {
    /* counter width + header h3 margin-left */
    margin-left: 90px;
  }
  .article-text.compact {
    margin-left: 0;
    margin-right: 230px;
  }
}

.social-medias {
  /* margin: 0 20px; */
  /* width: 200px; */
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.social-medias__item {
  margin-right: 10px;
  width: 30px;
  height: 30px;
}
.social-medias__item > svg {
  width: 100%;
  height: 100%;
}
.social-medias__item path {
  transition: 0.2s fill;
}
.social-medias__item:hover path,
.social-medias__item:active path,
.social-medias__item:focus path {
  fill: var(--primary-orange);
}

main>*{
  margin-top: 10px;
  margin-bottom: 10px;
} 
main>h2{
  margin-top: 130px;
  margin-bottom: 30px;
}
main img, main .round{
  margin: auto;
}
.statistics>img{
  width: 250px;
}
@media screen and (min-width: 1000px){
  .statistics>img{
    width: auto;
  }
  .main-grid{
    display: grid;
    grid-template-columns:[left] 1fr [right] 1fr;
    text-align: left;
    grid-column-gap: 100px;
    -webkit-column-gap: 100px;
            column-gap: 100px;
  }
  .grid-section > .btn{
    float: left;
  }
  .order1{
    -webkit-order: -1;
            order: -1;
  }

  .opinions + .btn{
    margin-bottom: 140px;
  }
}

.opinions-item {
  position: relative;
  background: var(--neutral-pale-gray);
  margin-top: 50px;
  padding: 40px 25px 25px;
  width: 330px;
}
.avatar {
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 70px;
}
@media screen and (min-width: 1000px) {
  .opinions-item {
    width: 740px;
  }
}

.slideshow {
  margin: 0 calc(-1 * var(--padding-horizontal-mobile)) 50px;
}
/* .active {
  background-color: red;
} */
.slideshow-mask {
  overflow: hidden;
  width: 100%;
  max-width: 2500px;
}
.slideshow-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  grid-gap: 20px;
  gap: 20px;
  transition: -webkit-transform 1.5s cubic-bezier(0.25, 0.1, 0.32, 1.1);
  transition: transform 1.5s cubic-bezier(0.25, 0.1, 0.32, 1.1);
  transition: transform 1.5s cubic-bezier(0.25, 0.1, 0.32, 1.1), -webkit-transform 1.5s cubic-bezier(0.25, 0.1, 0.32, 1.1);
  min-height: 50px;
}
.slideshow-item {
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  -webkit-flex-grow: 0;
          flex-grow: 0;
}
.slideshow-dot {
  cursor: pointer;
  display: inline-block;
  width: 15px;
  height: 15px;
  margin: 0 5px;
  border-radius: 100%;
  background: white;
  border: 2px solid var(--primary-orange);
}
.slideshow-dot.active {
  background: var(--primary-orange);
}

@media screen and (min-width: 1000px) {
  .slideshow {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 0 calc(-1 * var(--padding-horizontal-desktop)) 50px;
  }
}

.fancy-container {
  color: white;
  margin: 50px calc(-1 * var(--padding-horizontal-mobile)) 0;
  background-color: var(--primary-orange);
  background-image: url(../../static/media/bg-simplify-section-mobile.a79c8471.svg);
  background-repeat: no-repeat;
  background-position-y: 30px;
  padding: 90px 50px 70px;
  /* background: url('../../images/bg-simplify-section-mobile.svg'); */
}
@media screen and (max-width: 1000px) {
  .fancy-container > h2 {
    font-size: 2em;
  }
}
@media screen and (min-width: 1000px) {
  .fancy-container {
    margin: 50px calc(-1 * var(--padding-horizontal-desktop)) 0;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 60px 200px;
    background: unset;
  }
  .fancy-container::before {
    z-index: -1;
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-transform: scale(-1, -1);
            transform: scale(-1, -1);
    background-color: var(--primary-orange);
    background-image: url(../../static/media/bg-simplify-section-desktop.ca40c1ee.svg);
    background-position: calc(100% - 330px) -20px;
    background-size: 100%;
    background-repeat: no-repeat;
  }
  .fancy-container > .round,
  .fancy-container > * {
    margin: 0;
  }
  .fancy-container > * {
    text-align: left;
  }
  .fancy-container > h2 {
    width: 550px;
  }
}

